  //       'A blended whiskey (or blended whisky) is the product of blending different types of whiskeys and sometimes also neutral grain spirits, coloring, and flavourings. It is generally the product of mixing one or more higher-quality straight or single malt whiskies with less expensive spirits and other ingredients. This allows for a lower priced product, though expensive "premium"varieties also exist.\r\n\r\nThe use of neutral grain spirit in producing blended whiskey lowers the cost of production because the grain is typically cheaper than malted barley. Blended whiskeys are typically not as expensive or rare as single or straight whiskeys. The precise identity of the blended whiskeys used in production may be undisclosed to protect the trade secrets of the blender. Depending on the local regulations, a whiskey that includes grain spirit may be labeled as a "blended whiskey"or a "spirit whiskey", but in either case, the use of the neutral grain spirit is generally mentioned on the bottle or label.',
