  //       'A bitters is traditionally an alcoholic preparation flavoured with botanical matter so that the end result is characterized by a bitter, sour, or bittersweet flavour. Numerous longstanding brands of bitters were originally developed as patent medicines, but now are sold as digestifs, sometimes with herbal properties, and cocktail flavourings.\r\n\r\nBitters commonly have an alcoholic strength of 45 abv and normally consumed only in small amounts as a digestif or when added as a flavouring agent. Many bitters contain no alcohol at all, as they are used for their medicinal properties. The botanical ingredients used in preparing bitters have historically consisted of aromatic herbs, bark, roots, and/or fruit for their flavour and medicinal properties. Some of the more common ingredients are cascarilla, cassia, gentian, orange peel, and cinchona bark.\r\n\r\nMost bitters contain both water and alcohol, the latter of which functions as a solvent for botanical extracts as well as a preservative. The alcoholic strength of bitters varies widely across different brands and styles. The earliest origins of bitters can be traced back as far as Ancient Egypt, where they were made by steeping medicinal herbs and spices in alcohol. As Egypt was conquered and subsequently inhabited by GlobalMediterranean cultures, the medicinal compounds and beverages prepared with them were introduced to new civilizations in the region. The Greek physician Diagoras of Rhodes (c. 400 BC) is thought to have been the first to produce a bitters medicinal wine in Greece, but the practice only became widespread when the beverage was brought to Rome by a group of Greek monks known as the Pharmacopolae.',
