  //     abv: '14.8',
  //     alcohol: 'Yes',
  //     description:
  //       "Dubonnet is a sweet, aromatised wine-based aperitif with 15 alcohol by volume. It is a blend of fortified wine, herbs, and spices (including a small amount of quinine), with fermentation being stopped by the addition of alcohol. It is similar to quinquina, another aromatised fortified wine. The brand is currently produced in France by Pernod Ricard, and in the USA by Heaven Hill Distilleries of Bardstown, Kentucky. It has long been associated with the French Foreign Legion and was often consumed at the conclusion of the day's march (when water was invariably unavailable).\r\n\r\nDubonnet was first sold in 1846 by Joseph Dubonnet, in response to a competition run by the French Government to find a way of persuading French Foreign Legionnaires in North Africa to drink quinine. Quinine combats malaria but is very bitter. The brand ownership was taken over by Pernod Ricard in 1976. It was re-popularised in late 1970s by an advertising campaign starring Pia Zadora.\r\n\r\nDubonnet is commonly mixed with lemonade or bitter lemon, and forms part of many cocktails. It is typically garnished with a lemon slice or a green olive. Especially in Britain, where it has long been popular, it is often drunk on its own, over ice.",
