  //     abv: '40',
  //     alcohol: 'Yes',
  //     description:
  //       'Irish whiskey (Irish: Fuisce or uisce beatha) is whiskey made on the island of Ireland.\r\n\r\nThe word "whiskey"is an Anglicisation of the first word in the Gaelic phrase, uisce beatha, meaning "water of life"(modern Irish: uisce beatha, Scottish: uisge beatha and Manx: ushtey bea). The phrase was a translation of the Latin term aqua vitae, which was commonly used to describe distilled spirits during the Middle Ages.\r\n\r\nPeat is rarely used in the malting process, so that Irish whiskey has a smoother finish as opposed to the smoky, earthy overtones common to some Scotches. There are notable exceptions to these rules in both countries; an example is Connemara peated Irish malt (double distilled) whiskey from the Cooley Distillery in Riverstown, Cooley, County Louth.\r\n\r\nIrish whiskey was once the most popular spirit in the world, though a long period of decline from the late 19th century onwards greatly damaged the industry. So much so that although Ireland boasted over 30 distilleries in the 1890s, a century later, this number had fallen to just three. However, Irish whiskey has seen a great resurgence in popularity since the late twentieth century, and has been the fastest growing spirit in the world every year since 1990. With exports growing by over 15 per annum in recent years, existing distilleries have been expanded and a number of new distilleries constructed. As of early 2017, Ireland now has sixteen distilleries in operation, with at least a further fourteen in the planning stages.',
  //     type: 'Whiskey',
  //     flavour: 'grain',
  //     country: 'Ireland',
