  //     abv: '35',
  //     alcohol: 'Yes',
  //     description:
  //       'Applejack is a strong apple-flavoured alcoholic drink produced from apples, popular in the American colonial period. The name derives from jacking, a term for increasing ("jacking up") the alcohol content of cider by freezing it and removing the ice, a practice that goes back centuries. Applejack was historically made by concentrating cider, either by the traditional method of freeze distillation or by true evaporative distillation. The term applejack derives from jacking, a term for increasing ("jacking up") the alcohol content of cider by freezing it and removing the ice, a practice that goes back centuries. Starting with the fermented juice of apples, cider, they "jacked"it (freeze distilled), which increased the alcohol content, making it easier to store during the winter. The term "applejack"derives from the word "jacking", a term for freeze distillation. The modern product sold as "applejack"is no longer produced by jacking but rather by blending apple brandy and neutral spirits.',
