  //     abv: '0',
  //     alcohol: 'No',
  //     description:
  //       'Carbonated water (also known as club soda, soda water, sparkling water, seltzer water, or fizzy water, or the closely related "tonic water") is water into which carbon dioxide gas under pressure has been dissolved. Some of these have additives, such as sodium chloride, sodium bicarbonate or similar, but seltzer water is almost always composed of water and carbon dioxide with no other additives. Carbonated water is a refreshing beverage and a good alternative to sugary soft drinks. However, concerns have been raised that it may be bad for your health.\r\n\r\nCarbonated water is water that has been infused with carbon dioxide gas under pressure. This produces a bubbly drink that’s also known as sparkling water, club soda, soda water, seltzer water, and fizzy water. Apart from seltzer water, carbonated waters usually have salt added to improve their taste.\r\n\r\nCarbonated water is a refreshing beverage and a good alternative to sugary soft drinks. However, concerns have been raised that it may be bad for your health.',
