  //     'thumb-small':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Coffee_brandy-Small.png',
  //     abv: '25',
  //     alcohol: 'Yes',
  //     description:
  //       'Coffee liqueur is a liqueur that is flavoured with coffee. Several brands of coffee liqueur are commercially available; a few examples include Kahlúa, Tia Maria, and Toschi Caffè. Coffee liqueurs are considered to be part of the wider category of liqueurs which are spirits with natural or artificial flavours that contain at least 2.5 percent sugar by weight. Coffee liqueurs are often mixed with cream or milk to make Globalcocktails, such as the White Russian or the Espresso Martini.',
  //     type: 'Brandy',
  //     flavour: 'coffee',
  //     country: 'United States',
  //   },
  //   {
  //     _id: {
  //       $oid: '64aebb7f82d96cc69e0eb4ca',
  //     },
  //     title: 'Red wine',
  //     ingredientThumb:
  //       'https://ftp.goit.study/img/drinkify/ingredients/Red_wine.png',
  //     'thumb-medium':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Red_wine-Medium.png',
  //     'thumb-small':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Red_wine-Small.png',
  //     abv: '14',
  //     alcohol: 'Yes',
  //     description:
  //       'Wine (from Latin vinum) is an alcoholic beverage made from grapes, generally Vitis vinifera, fermented without the addition of sugars, acids, enzymes, water, or other nutrients.\r\n\r\nYeast consumes the sugar in the grapes and converts it to ethanol and carbon dioxide. Different varieties of grapes and strains of yeasts produce different styles of wine. These variations result from the complex interactions between the biochemical development of the grape, the reactions involved in fermentation, the terroir, and the production process. Many countries enact legal appellations intended to define styles and qualities of wine. These typically restrict the geographical origin and permitted varieties of grapes, as well as other aspects of wine production. Wines not made from grapes include rice wine and fruit wines such as plum, cherry, pomegranate and elderberry.\r\n\r\nWine has been produced for thousands of years. The earliest known traces of wine are from China (c. 7000 BC), Georgia (c. 6000 BC), Iran (c. 5000 BC), and Sicily (c. 4000 BC). The earliest known winery is the 6,100-year-old Areni-1 winery in Armenia. Wine reached the Balkans by 4500 BC and was consumed and celebrated in ancient Greece, Thrace and Rome. Throughout history, wine has been consumed for its intoxicating effects.\r\n\r\nWine has long played an important role in religion. Red wine was associated with blood by the ancient Egyptians and was used by both the Greek cult of Dionysus and the Romans in their Bacchanalia; Judaism also incorporates it in the Kiddush and Christianity in the Eucharist.',
  //     type: 'Wine',
  //     flavour: 'grape',
  //     country: 'United States',
  //   },
  //   {
  //     _id: {
  //       $oid: '64aebb7f82d96cc69e0eb4cb',
  //     },
  //     title: 'Rum',
  //     ingredientThumb:
  //       'https://ftp.goit.study/img/drinkify/ingredients/Rum.png',
  //     'thumb-medium':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Rum-Medium.png',
  //     'thumb-small':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Rum-Small.png',
  //     abv: '40',
  //     alcohol: 'Yes',
  //     description:
  //       'Rum is a distilled alcoholic beverage made from sugarcane byproducts, such as molasses, or directly from sugarcane juice, by a process of fermentation and distillation. The distillate, a clear liquid, is then usually aged in oak barrels.\r\n\r\nThe majority of the world\'s rum production occurs in the Caribbean and Latin America. Rum is also produced in Scotland, Austria, Spain, Australia, New Zealand, Fiji, the Philippines, India, Reunion Island, Mauritius, South Africa, Taiwan, Thailand, Japan, theGlobal, and Canada.\r\n\r\nRums are produced in Globalgrades. Light rums are commonly used in cocktails, whereas "golden"and "dark"rums were typically consumed straight or neat, on the rocks, or used for cooking, but are now commonly consumed with mixers. Premium rums are also available, made to be consumed either straight or iced.\r\n\r\nRum plays a part in the culture of most islands of the West Indies as well as in The Maritimes and Newfoundland. This beverage has famous associations with the Royal Navy (where it was mixed with water or beer to make grog) and piracy (where it was consumed as bumbo). Rum has also served as a popular medium of economic exchange, used to help fund enterprises such as slavery (see Triangular trade), organized crime, and military insurgencies (e.g., the American Revolution and Australia\'s Rum Rebellion).\r\n\r\nThe precursors to rum date back to antiquity. Development of fermented drinks produced from sugarcane juice is believed to have first occurred either in ancient India or in China, and to have spread from there. An example of such an early drink is brum. Produced by the Malay people, brum dates back thousands of years. Marco Polo also recorded a 14th-century account of a "very good wine of sugar"that was offered to him in the area that became modern-day Iran.\r\n\r\nThe first distillation of rum took place on the sugarcane plantations of the Caribbean in the 17th century. Plantation slaves first discovered molasses, a byproduct of the sugar refining process, could be fermented into alcohol. Later, distillation of these alcoholic byproducts concentrated the alcohol and removed impurities, producing the first true rums. Tradition suggests rum first originated on the island of Barbados. However, in the decade of the 1620s, rum production was recorded in Brazil. A liquid identified as rum has been found in a tin bottle found on the Swedish warship Vasa, which sank in 1628.\r\n\r\nA 1651 document from Barbados stated, "The chief fuddling they make in the island is Rumbullion, alias Kill-Divil, and thi is made of sugar canes distilled, a hot, hellish, and terrible liquor."',
  //     type: 'Rum',
  //     flavour: 'sugar',
  //     country: 'United States',
  //   },
