  //     abv: '40',
  //     alcohol: 'Yes',
  //     description:
  //       'Cognac (/ˈkɒnjæk/ KON-yak or /ˈkoʊnjæk/ KOHN-yak; French pronunciation: ​[kɔ.ɲak]) is a variety of brandy named after the town of Cognac, France. It is produced in the surrounding wine-growing region in the departments of Charente and Charente-Maritime.\r\n\r\nCognac production falls under French Appellation d\'origine contrôlée designation, with production methods and naming required to meet certain legal requirements. Among the specified grapes Ugni blanc, known locally as Saint-Emilion, is most widely used. The brandy must be twice distilled in copper pot stills and aged at least two years in French oak barrels from Limousin or Tronçais. Cognac matures in the same way as whiskies and wine barrel age, and most cognacs spend considerably longer "on the wood"than the minimum legal requirement.\r\n\r\nCognac is a type of brandy and, after the distillation and during the aging process, is also called eau de vie. It is produced by doubly distilling white wines produced in any of the designated growing regions.',
