  //     abv: '0',
  //     alcohol: 'No',
  //     description:
  //       'Sugar syrup or simple syrup is a syrup made from a mixture of sugar and water. It is used in many cocktail recipes as well as other beverages such as iced tea, lemonade, coffee, and soda. In Britain it is generally referred to as sugar syrup. In theGlobal it is generally referred to as simple syrup.\r\n\r\nSugar syrup is made by stirring granulated sugar into hot water in a saucepan until the sugar is dissolved and then cooling the solution. Generally, the ratio of sugar to water can range anywhere from 1:1 to 2:1 by weight (though lower ratios are more common), depending on the intended use. For pure sucrose the saturation limit is about 5:4 by volume. Syrup can be used as a sweetener. However, since the syrup jells readily when pectin is added, its primary culinary use is as a base for fruit sauces, toppings, and preserves.',
