  //       'Ginger (Zingiber officinale) is a flowering plant whose rhizome, ginger root or simply ginger, is widely used as a spice or a folk medicine.\r\n\r\nIt is a herbaceous perennial which grows annual stems about a meter tall bearing narrow green leaves and yellow flowers. Ginger is in the family Zingiberaceae, to which also belong turmeric (Curcuma longa), cardamom (Elettaria cardamomum), and galangal. Ginger originated in the tropical rainforest in Southern Asia. Although ginger no longer grows wild, it is thought to have originated on the Indian subcontinent. The ginger plants grown in India show the largest amount of genetic variation. The larger the number of genetic variations, the longer the plant has grown in that region. Ginger was exported to Europe via India in the first century AD as a result of the lucrative spice trade and was used extensively by the Romans. India is now the largest producer of ginger.\r\n\r\nOther members of the family Zingiberaceae include turmeric, cardamom, and galangal. The distantly related dicots in the genus Asarum are commonly called wild ginger because of their similar taste.',
