  //     abv: '38',
  //     alcohol: 'Yes',
  //     description:
  //       "Peach vodka is a flavoured spirit that combines the essence of peaches with the neutral character of vodka. It's known for its fruity and slightly sweet flavour, making it a popular choice for cocktails and mixed drinks that require a peachy twist. Peach vodka is often enjoyed during the summer months and is used to add a fruity element to Globalcocktails. ",
