  //       'Everclear is a brand of high-proof grain alcohol (ethanol) that is known for its very high alcohol content. It is often used as a base for cocktails, mixed drinks, and tinctures due to its potency. Everclear is available in different proof levels, with the most common varieties being 151 and 190 proof (75.5 and 95 alcohol by volume, respectively).',
