  //     'thumb-small':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Firewater-Small.png',
  //     abv: '0',
  //     alcohol: 'No',
  //     description:
  //       "Firewater is a colloquial term often used to describe very strong and high-proof alcoholic beverages. The term is usually associated with spirits that have a high alcohol content and are known to have a burning sensation when consumed due to their potency. Firewater is not a specific type of drink or a brand but rather a description of intensely alcoholic beverages. It's important to note that consuming such high-proof spirits can be dangerous and is often not recommended in excessive amounts due to the potential for health risks and intoxication.",
  //     type: 'Grain Alcohol',
  //     flavour: 'Flavor',
  //     country: 'United States',
  //   },
  //   {
  //     _id: {
  //       $oid: '64aebb7f82d96cc69e0eb4f8',
  //     },
  //     title: 'Lemonade',
  //     ingredientThumb:
  //       'https://ftp.goit.study/img/drinkify/ingredients/Lemonade.png',
  //     'thumb-medium':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Lemonade-Medium.png',
  //     'thumb-small':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Lemonade-Small.png',
  //     abv: '0',
  //     alcohol: 'No',
  //     description:
  //       'Lemonade is any of Globalsweetened beverages found around the world, all characterized by lemon flavour.\r\n\r\nMost lemonade varieties can be separated into two distinct types: cloudy and clear; each is known simply as "lemonade"(or a cognate) in countries where dominant. Cloudy lemonade, generally found in North America and India, is a traditionally homemade drink made with lemon juice, water, and sweetener such as cane sugar or honey. Found in the United Kingdom, Ireland, South Africa, Australia, and New Zealand, clear lemonade is a lemon flavoured carbonated soft drink. It should not be confused with Sprite, a lemon-lime flavoured soft drink.\r\n\r\nA popular cloudy variation is pink lemonade, made with added fruit flavours such as raspberry or strawberry among others, giving a distinctive pink color. The "-ade"suffix may also be applied to other similar drinks made with different fruits, such as limeade, orangeade, or cherryade. Alcoholic varieties are known as hard lemonade.\r\n\r\nIn many European countries, the French word limonade has come to mean "soft drink", regardless of flavour.[citation needed]',
