  //     abv: '40',
  //     alcohol: 'Yes',
  //     description:
  //       'Absolut Vodka is a brand of vodka, produced near Åhus, in southern Sweden. Absolut is owned by French group Pernod Ricard; they bought Absolut for €5.63 billion in 2008 from the Swedish state. Absolut is the third largest brand of alcoholic spirits in the world after Bacardi and Smirnoff, and is sold in 126 countries.\r\n\r\nAbsolut was established in 1879 by Lars Olsson Smith and is produced in Åhus, Sweden.[1] Smith challenged the city of Stockholm\'s liquor marketing monopoly with his vodka. It was sold at a lower price than the monopoly\'s product, just outside the city border. Smith even offered free boat rides to the distillery and "Rent Brännvin"made Smith a fortune.\r\n\r\nIn 1917, the alcohol industry in Sweden was monopolized by the Swedish government. Vodka was then sold nationwide under the name "Absolut Rent Brännvin". The name changed with intervals, Renat Brännvin or Absolut Rent Brännvin. In 1979, the old name Absolut was picked up when the upper-price range ABSOLUT VODKA was introduced. Renat is still a euphemism for spirits in Sweden, and the name of another vodka product by Vin & Sprit.\r\n\r\n\r\nAbsolut Vodka was introduced to the global market in 1979. Since its launch, Absolut has grown from 90,000 liters to 96.6 million liters in 2008. It has become the largest international spirit and is available in 126 countries. The vodka is made from winter wheat. Approximately 80,000 metric tons (2,900,000 bushels) of wheat are used annually to produce Absolut Vodka. Over one kilogram of grain is used in every one-liter bottle.',
  //     type: 'Vodka',
  //     flavour: 'International spirit',
  //     country: 'Sweden',
