  //     title: 'Coconut milk',
  //     description:
  //       "Coconut milk is the liquid that comes from the grated meat of a mature coconut. The opacity and rich taste of coconut milk are attributed to its high oil content, most of which is saturated fat. Coconut milk is a popular food ingredient used in Southeast Asia, the Caribbean, and northern South America.\r\n\r\nCoconut milk is distinguished from coconut water by its thicker consistency and milkier appearance.\r\n\r\nCoconut milk is an opaque, milky-white liquid extracted from the grated pulp of mature coconuts. The opacity and rich taste of coconut milk are due to its high oil content, most of which is saturated fat. Coconut milk is a traditional food ingredient used in Southeast Asia, Oceania, South Asia, and East Africa. It is also used for cooking in the Caribbean, tropical Latin America, and West Africa, where coconuts were introduced during the colonial era.\r\n\r\nCoconut milk is traditionally made by grating the white inner flesh of a brown coconut and mixing the shredded coconut pulp with a small amount of hot water in order to suspend the fat present in the grated pulp. The grating process itself can be carried out manually or by comminution, a process that uses a more modern grating machine to facilitate the grating.\r\n\r\nCoconut milk exists in two grades: thick and thin. Thick coconut milk contains 20-22% fat while thin coconut milk contains 5-7% fat. Thick milk is prepared by directly squeezing grated coconut meat through cheesecloth. The squeezed coconut meat is then soaked in warm water and squeezed a second or third time for thin coconut milk. Thick milk is mainly used to make desserts, as well as rich and dry sauces. Thin milk is used for soups and general cooking. This distinction is usually not made in Western nations, since fresh coconut milk is rare; in most consumers' minds, coconut milk is generally associated with canned coconut milk.",
