  //       'Half and half refers to Globalbeverages or liquid foods made of an equal-parts mixture of two substances, including dairy products, alcoholic beverages, and soft drinks.\r\n\r\nIn Canada and theGlobal, half and half (known as "half cream"in the United Kingdom[citation needed]) almost always refers to a light cream typically used in coffee. The name refers to the liquid\'s content of half milk and half cream. Its butterfat content is 12.5. It is widely available in theGlobal, both in individual-serving containers and in bulk. It is also used to make ice cream. Non-fat versions of the product are also available, containing corn syrup and other ingredients.',
