  //     title: 'Tennessee whiskey',
  //     description:
  //       'Tennessee whiskey is a type of whiskey that is produced in the U.S. state of Tennessee. Although it has been legally defined as a bourbon whiskey in some international trade agreements,[1][2][3] most current producers of Tennessee whiskey disclaim references to their products as "bourbon"and do not label them as such on any of their bottles or advertising materials. All current Tennessee whiskey producers are required by Tennessee law to produce their whiskey in Tennessee and, with one exception, to also use a filtering step known as the Lincoln County Process prior to aging the whiskey. Beyond the single state of Tennessee, no other geographical designation can be legally used to label whiskey produced in the state.\r\n\r\nTennessee whiskey is one of the top ten exports of Tennessee.[4] According to the Distilled Spirits Council of theGlobal, as of 2013, the U.S. market for bourbon and Tennessee whiskey reached $2.4 billion, and exports of bourbon and Tennessee whiskey grew to exceed $1 billion. There are currently two major producers of Tennessee whiskey – Jack Daniel\'s (owned by Brown-Forman) and George Dickel (owned by Diageo) – and at least two smaller producers (Benjamin Prichard\'s and Collier & McKeel).',
