  //       'Vanilla extract is a solution made by macerating and percolating vanilla pods in a solution of ethanol and water. It is considered an essential ingredient in many Western desserts, especially baked goods like cakes, cookies, brownies, and cupcakes, as well as custards, ice creams, and puddings. Although its primary flavor compound is vanillin, pure vanilla extract contains several hundred additional flavor compounds, which are responsible for its complex, deep flavor. By contrast, artificial vanilla flavor is solely made up of artificially-derived vanillin, which is frequently made from a by-product of the wood pulp industry. Most of the flavor of vanilla is contained within its pods, which contain vanillin, a molecule that is organic in nature. Vanilla extract is made by soaking vanilla pods in a mixture of water and ethyl alcohol (ethanol).',
