  //     title: 'Black Sambuca',
  //     description:
  //       'black samb uca is a variety of sambuca, a liqueur flavored with essential oils obtained from star anise, with added licorice and other flavorings. It is deep blue in color. Like other anise-flavored liqueurs, the ouzo effect is sometimes observed when combined with water. The oils are soluble in ethanol but not in water. When the clear liqueur is diluted with water, the oils cloud the liqueur, a phenomenon known as the ouzo effect. This same effect occurs with absinthe and similar drinks. The oils solidify when cold water is added and the liqueur may turn cloudy or "milky". This is because the anethole in the anise remains in solution above its melting point of 21 °C. Anethole is only slightly soluble in water but highly soluble in ethanol. When the solution is diluted with water, the anethole returns to solution causing an emulsion to form, which is what causes the liqueur to turn cloudy. The oils are soluble in ethanol but not in water. The water content causes the ouzo effect.',
