  //       'Lemonade is the name for a number of sweetened beverages found around the world, all characterized by lemon flavor. Most lemonade varieties can be separated into two distinct types: cloudy and clear; each is known simply as "lemonade"(or a cognate) in countries where dominant.\r\n\r\nCloudy lemonade, generally found in North America and India, is traditionally a homemade drink using lemon juice, water, and a sweetener such as cane sugar or honey. Such beverages are also commercially produced, as well as produced in the home. Generally served cold, cloudy lemonade may also be served hot as a remedy for congestion and sore throats, frozen, or used as a mixer. Traditionally, it is common for children in US and Canadian neighborhoods to start lemonade stands to make money during the summer months.\r\n\r\nThe most common form of lemonade found in the US, Canada, and India, cloudy lemonade, also known as "traditional lemonade"in the UK and Australia, is typically non-carbonated and made with fresh lemon juice; however commercially produced varieties are also available. It is traditionally a homemade drink using lemon juice, water, and a sweetener such as cane sugar, simple syrup or honey. In the United Kingdom and Australia, clear lemonade, a carbonated drink, is more common.',
