  //       'Fruit brandy is a distilled beverage produced from mash, juice, wine or residues of culinary fruits. The term covers a broad class of spirits produced across the world, and typically excludes beverages made from grapes, which are referred to as plain brandy (when made from distillation from wine) or pomace brandy (when made directly from grape pomace). Apples, pears, apricots, plums and cherrys are the most commonly used fruits. In British usage, fruit brandy may also refer to liqueurs obtained by maceration of whole fruits, juice or flavoring in a distilled beverage, for example cherry brandy (see below). Such beverages are used similar to cordials, and as an ingredient in cocktails and desserts.\r\n\r\nTypically, this spirit is clear and colourless, but some fruit brandies obtain substantial colour from the fruit skin, pulp or juice, and variously take on the colour of the distillate, such as the amber hue of apricot brandy or the yellow straw colour of cherry brandy, or are dyed with colours. Fruit brandy usually contains 40% to 45% ABV (80 to 90 US proof).',
