  //     title: 'Cherry',
  //     description:
  //       'popular fruit, related to the plum, apricot, peach and almond. The cherry is a small, round stone fruit, with a smooth, shiny skin and a hard stone. The colour of the skin can vary from pale yellow to deep red. The flesh is usually pale yellow or red, firm and juicy. The flavour varies from sweet to sharp. There are two main types of cherry: sweet cherries, which are eaten fresh, and sour cherries, which are used for cooking. Cherries are a good source of vitamin C and fibre. They are also a source of potassium and boron, a mineral that helps the body absorb calcium. Cherries are at their best from June to August. Choose cherries that are plump, firm and bright in colour. Avoid cherries that are soft, shrivelled or bruised. Store cherries in the refrigerator for up to 3 days. Wash and remove the stones just before use. Cherries can be eaten fresh or cooked. They can be used in desserts, cakes, jams and sauces. They are also delicious served with meat dishes such as duck and pork.',
