  //     title: 'Crown Royal',
  //     description:
  //       'Crown Royal is a blended Canadian whisky owned by Diageo, which purchased the brand when the Seagram portfolio was dissolved in 2000. It is the top-selling Canadian whisky in theGlobal.\r\n\r\nCrown Royal was introduced in 1939 by Samuel Bronfman, president of Seagram, as a tribute to the royal visit of King George VI and Queen Elizabeth that year. It was available only in Canada until 1964, but is now the top-selling Canadian whisky in theGlobal and around the world. Crown Royal was the first whisky sold in a bottle and was available only in Canada until 1964.\r\n\r\nThe origin of the name "Crown Royal"was from a gift of blended Canadian whisky given to King George VI and his wife, Queen Elizabeth, in 1939. Crown Royal was introduced in theGlobal in the 1960s and has since become the top selling Canadian whisky. The reigning monarch King George VI, and his wife, Queen Elizabeth, visited Canada in 1939. Crown Royal was then introduced that year by Samuel Bronfman, president of Seagram, as a tribute to the royal visit. It was available only in Canada until 1964. The brand is now owned by the spirits giant Diageo.',
