  //       'Citrus × sinensis, also known as the Citrus aurantium Sweet Orange Group,[1] includes the commonly cultivated sweet orange, blood oranges, and navel oranges.\r\n\r\nThe orange fruit is an important agricultural product, used for both the juicy fruit pulp, the thin bitter rind (see orange peel) and the aromatic orange-flower oil. These oils are used in foods, perfumes and cosmetics, as well as used as a solvent. Orange blossoms are used in several different ways, as are the leaves and wood of the tree. The orange blossom, which is the state flower of Florida, is highly fragrant and traditionally associated with good fortune. It has long been popular in bridal bouquets and head wreaths. Orange blossom essence is an important component in the making of perfume.',
