  //       'Almond extract is a concentrated flavoring used in food preparation. It is made from almond oil and alcohol (hence, it is not a "pure extract"in theGlobal). It is usually clear in color, unless caramel coloring is added. It generally tastes slightly of almonds, and is usually used in foods that do not have a strong almond component.\r\n\r\nAlmond extract is commonly used in baking, for example, in macarons, and is one of the most common flavors of baklava. It is also used in marzipan, which is used as a filling in pastries and confections. Almond extract is also used in almond paste, a key ingredient in frangipane and many other European-style pastries and filled cakes. Almond extract is also sometimes used in American-style vanilla ice cream and other desserts, as well as in some cocktails.',
