  //       'Crushed red pepper or red pepper flakes is a condiment or spice consisting of dried and crushed (as opposed to ground) red chili peppers.\r\n\r\nThis condiment is most often produced from cayenne-type peppers, although commercial producers may use a variety of different cultivars, usually within the 30,000–50,000 Scoville unit range. The village of Bukovo, North Macedonia, is often credited with the creation of crushed red pepper.\r\n\r\nCrushed red pepper shakers have become as standard as salt and pepper on tables at Italian style restaurants and especially pizza parlors in theGlobal. Often there is a high ratio of seeds, which some people believe intensifies the heat of this flavorful condiment. The chili pepper is considered to be a member of the nightshade family.',
