  //       'Allspice, also called Jamaica pepper, pepper, myrtle pepper, pimenta,[1] Turkish Yenibahar, or newspice, is a spice that is the dried unripe fruit (berries, used as a spice) of Pimenta dioica, a midcanopy tree native to the Greater Antilles, southern Mexico, and Central America, now cultivated in many warm parts of the world.[2] The name "allspice"was coined as early as 1621 by the English, who thought it combined the flavour of cinnamon, nutmeg, and cloves.[3] Several unrelated fragrant shrubs are called "Carolina allspice"(Calycanthus floridus), "Japanese allspice"(Chimonanthus praecox), or "wild allspice"(Lindera benzoin).',
