  //       'https://ftp.goit.study/img/drinkify/ingredients/Banana_liqueur-Small.png',
  //     title: 'Banana liqueur',
  //     description:
  //       "Banana liqueur is an alcoholic liqueur flavored with banana, either artificially or with real banana juice. It is used in tropical drinks and desserts that taste like bananas. A typical banana liqueur is yellow in color and tastes like bananas. It is difficult to produce a banana-flavored liqueur that tastes like bananas, since bananas have a relatively subtle taste. The exception to this is creme de banane liqueurs, which are artificially flavored with a strong taste of bananas. Banana liqueurs are produced industrially by a number of brands, such as Bols, DeKuyper, Hiram Walker and Marie Brizard.\r\n\r\nBanana liqueur is used in a multitude of cocktails, such as the Banana Boat, the Banshee, the Banana Daiquiri, the Banana Mama, the Banana Split, the Dirty Monkey, the Hummingbird, the Painkiller, the Pink Squirrel, the Banshee, the Yellow Bird, and the Zippy's Revenge. It is also used as an ingredient in desserts, such as the banana martini and the flaming banana.\r\n\r\nBanana liqueur is produced by several manufacturers, such as Bols, DeKuyper, Hiram Walker, Marie Brizard, and Teichenné. Bols is the oldest distillery brand currently producing banana liqueur. Bols Crème de Bananes was developed in the 1950s by John Bols, son of the company's founder Lucas Bols. Bols Crème de Bananes is made from bananas, lemon juice, sugar and alcohol. DeKuyper produces a creme de banane liqueur made with natural flavors and artificial coloring. Hiram Walker produces a banana schnapps made with natural flavors and artificial coloring. Marie Brizard produces a creme de banane liqueur made with natural flavors and artificial coloring. Teichenné produces a banana schnapps made with natural flavors and artificial coloring.",
