  //     title: 'Fresca',
  //     description:
  //       'Fresca is a diet lime and grapefruit citrus soft drink made by The Coca-Cola Company. Borrowing the word Fresca (meaning "fresh") from Spanish, it was first introduced in theGlobal in 1966. Originally only a sugar-free diet soda, a sugar sweetened version has been introduced in some markets since 2005.\r\n\r\nFresca has undergone several major ingredient changes since its introduction. The drink was originally sweetened with cyclamates, which were banned by the FDA in 1969, and replaced with saccharin. That was replaced by NutraSweet-brand aspartame. Beverages containing NutraSweet were banned by the FDA in 1983, but Fresca was exempt from the ban and survived using the sweetener until 2005, when it was replaced with Splenda. Fresca packaging has been redesigned several times, although advertising continues to emphasize sophistication.',
  //     type: 'Soda',
  //     alcohol: 'No',
  //     abv: '0',
