  //       'Surge is a citrus flavored soft drink first produced in the 1990s by The Coca-Cola Company to compete with Pepsi\'s Mountain Dew. Surge was advertised as having a more "hardcore"edge, much like Mountain Dew\'s advertising at the time, in an attempt to lure customers away from Pepsi. It was originally launched in Norway as Urge, and was so popular that it was later released in America as Surge. Lagging sales caused production to be ended in 2003 for most markets, and by 2014, it was only sold in Norway and in the Amazon.com online store. On September 15, 2014, The Coca-Cola Company announced that it would relaunch Surge in theGlobal via Amazon.com, twelve years after its discontinuation.',
