  //     title: 'Mountain Dew',
  //     description:
  //       'Mountain Dew (stylized as Mtn Dew) is a carbonated soft drink brand produced and owned by PepsiCo. The original formula was invented in 1940 by Tennessee beverage bottlers Barney and Ally Hartman. A revised formula was created by Bill Bridgforth in 1958. The rights to this formula were obtained by the Tip Corporation of Marion, Virginia. William H. "Bill"Jones of the Tip corporation further refined the formula, launching that version of Mountain Dew in 1961. On August 27, 1964, the Mountain Dew brand and production rights were acquired from Tip by the Pepsi-Cola company, at which point distribution expanded more widely across theGlobal and Canada.\r\n\r\nBetween the 1940s and 1980s, there was just one variety of Mountain Dew, which was citrus-flavored and caffeinated in most markets (see Caffeine-Free Mountain Dew below). Diet Mountain Dew was introduced in 1988, followed by Mountain Dew Red, which was introduced and subsequently discontinued in 1988. In 2001, a cherry flavor called Code Red debuted. This product line extension trend has continued, with expansion into specialty, limited time production, region-specific, and retailer-specific (Taco Bell, 7-Eleven) variations of Mountain Dew.',
