  //       "Pernod Ricard is a French company that produces distilled beverages. The company's eponymous products, Pernod Anise and Ricard Pastis, are both anise-flavoured pastis apéritifs and are often referred to simply as Pernod or Ricard. The company also produces several other types of pastis.\r\n\r\nIt is the world’s second-largest wine and spirits seller. After the banning of absinthe, Pernod Ricard was created from the Pernod Fils company, which had produced absinthe. Pernod Ricard owned the distilled beverage division of the former corporation Seagram (including brands like Chivas Regal), along with many other holdings. In 2005, the company acquired a British-based competitor, Allied Domecq plc.\r\n\r\nIn 2008, Pernod Ricard announced its acquisition of Swedish-based V&S Group, which produces Absolut Vodka, from the Swedish government. In 2013 Pernod Ricard joined leading alcohol producers as part of a producers' commitments to reducing harmful drinking.\r\n\r\nAs of 2015, India is the company's third largest market by value.",
