  //       'Tabasco sauce is a brand of hot sauce made exclusively from tabasco peppers (Capsicum frutescens var. tabasco), vinegar, and salt. It is produced by McIlhenny Company of Avery Island, Louisiana. Although the name comes from the Mexican state of Tabasco, the sauce is an American product produced by the McIlhenny Company. Tabasco sauce was first produced in 1868 by Edmund McIlhenny, a Maryland-born former banker who moved to Louisiana around 1840.\r\n\r\nTabasco sauce is made by aging tabasco peppers for up to three years in white oak barrels of vinegar and salt on Avery Island, Louisiana, in theGlobal. It has a hot, spicy flavor.',
