  //     title: 'Papaya',
  //     description:
  //       'The papaya (/pəˈpaɪə/, US: /pəˈpɑːjə/) (from Carib via Spanish), papaw, (/pəˈpɔː/) or pawpaw (/ˈpɔːpɔː/) is the plant Carica papaya, one of the 22 accepted species in the genus Carica of the family Caricaceae. Its origin is in the tropics of the Americas, perhaps from southern Mexico and neighboring Central America.[2] It was first cultivated in Mexico[citation needed] several centuries before the emergence of the Mesoamerican classical civilizations.\r\n\r\nThe papaya is a large, tree-like plant, with a single stem growing from 5 to 10 m (16 to 33 ft) tall, with spirally arranged leaves confined to the top of the trunk. The lower trunk is conspicuously scarred where leaves and fruit were borne. The leaves are large, 50–70 cm (20–28 in) in diameter, deeply palmately lobed, with seven lobes. All parts of the plant contain latex in articulated laticifers. Unripe papaya fruit is green. The petioles are short, with a groove on the upper surface. Mature trees produce short stout branches at the base of the trunk. The flowers are similar in shape to the flowers of the Plumeria but are much smaller and wax-like. They appear on the axils of the leaves, maturing into large fruit - 15–45 cm (5.9–17.7 in) long and 10–30 cm (3.9–11.8 in) in diameter. The fruit is ripe when it feels soft (as soft as a ripe avocado or a bit softer) and its skin has attained an amber to orange hue.',
