  //     title: 'Kool-Aid',
  //     description:
  //       'Kool-Aid is a brand of flavored drink mix owned by Kraft Heinz based in Chicago, Illinois. The powder form was created by Edwin Perkins in 1927 based upon a liquid concentrate called Fruit Smack. Production of the drink mix began in 1931.\r\n\r\nKool-Aid is known as Nebraska\'s official soft drink. Kool-Aid was invented by Edwin Perkins in Hastings, Nebraska. All of his experiments took place in his mother\'s kitchen. Its predecessor was a liquid concentrate called Fruit Smack. To reduce shipping costs, in 1927, Perkins discovered a way to remove the liquid from Fruit Smack, leaving only a powder. This powder was named Kool-Aid. Perkins moved his production to Chicago in 1931 and Kool-Aid was sold to General Foods in 1953. Hastings still celebrates a yearly summer festival called Kool-Aid Days on the second weekend in August, in honor of their city\'s claim to fame. Kool-Aid is known as Nebraska\'s official soft drink.\r\n\r\nKool-Aid is usually sold in powder form, in either packets or small tubs. The drink is prepared by mixing the powder with sugar (the packets contain the correct amount of sugar to match the powder), and water, typically by the pitcherful. The drink is usually served with ice or refrigerated and served chilled. Additionally, there are some sugar-free varieties of Kool-Aid. Kool-Aid also comes in single-serving "Jammers"and "Bursts". These are prepared by adding water and ice to the sealed bag.',
