  //     title: 'Dr. Pepper',
  //     description:
  //       'Dr Pepper is a carbonated soft drink marketed as having a unique flavor. The drink was created in the 1880s by Charles Alderton in Waco, Texas and first served around 1885. Dr Pepper was first nationally marketed in theGlobal in 1904, and is now also sold in Europe, Asia, North and South America, and Australia, as well as New Zealand and South Africa as an imported good. Variants include a version without high fructose corn syrup, Diet Dr Pepper, as well as a line of additional flavors, first introduced in the 2000s.\r\n\r\nTheories abound about the origins of the drink\'s name. One conjecture is the "pep"refers to pepsin. However, the Dr Pepper Museum in Waco, Texas, says the true origin is unknown, but that the service druggist in Morrison\'s Old Corner Drugstore, Wade Morrison, named it "Dr. Pepper"in 1885. As with Coca-Cola, the formula for Dr Pepper is a trade secret, and allegedly the recipe is kept as two halves in safe deposit boxes in two separate Dallas banks. Theories abound about the origins of the drink\'s name. One conjecture is the "pep"refers to pepsin. However, the Dr Pepper Museum in Waco, Texas, says the true origin is unknown, but that the service druggist in Morrison\'s Old Corner Drugstore, Wade Morrison, named it "Dr. Pepper"in 1885. As with Coca-Cola, the formula for Dr Pepper is a trade secret, and allegedly the recipe is kept as two halves in safe deposit boxes in two separate Dallas banks.',
