  //       "Peychaud's Bitters is an aromatic bitters, originally created around 1830 by Antoine Amédée Peychaud, a Creole apothecary from the French colony of Saint-Domingue (now Haiti) who settled in New Orleans, Louisiana in 1795. It is a gentian-based bitters, comparable to Angostura bitters, but with a predominant anise aroma combined with a background of mint. Peychaud's Bitters is one of the main ingredients in the Sazerac cocktail and can also be used in the Manhattan, and many other cocktails. Originally Peychaud's bitters were made with a brandy base, but the current version is made with neutral spirits. Peychaud's Bitters are produced by the Buffalo Trace Distillery in Frankfort, Kentucky.",
