  //     title: 'Hot Sauce',
  //     description:
  //       'Hot sauce, also known as chili sauce or pepper sauce, is any condiment, seasoning, or salsa made from chili peppers and other ingredients. A very popular addition to different types of food, hot sauce allows individuals to enhance the flavor of their meals. Many commercial varieties of mass-produced hot sauce exist. Some sauces are very hot, including habanero and Scotch bonnet peppers, which can be anywhere between 100,000 and 350,000 Scoville units.\r\n\r\nThe ingredients of hot sauces are usually a combination of chili peppers and one or more other ingredients. The peppers make up the bulk of the sauce, and usually rate between 200,000 and 350,000 Scoville units. Other ingredients may include vinegar, salt, sugar, garlic, or spices. The ingredients themselves may have natural variation, and may be augmented with artificial ingredients. Manufacturers use many different processes from aging in containers, to pureeing and cooking the ingredients to achieve a desired flavor and consistency.',
