  //     'thumb-small':
  //       'https://ftp.goit.study/img/drinkify/ingredients/Soy_Sauce-Small.png',
  //     title: 'Soy Sauce',
  //     description:
  //       'Soy sauce (also called soya sauce in British English) is a liquid condiment of Chinese origin, made from a fermented paste of soybeans, roasted grain, brine, and Aspergillus oryzae or Aspergillus sojae molds. Soy sauce in its current form was created about 2,200 years ago during the Western Han dynasty of ancient China, and spread throughout East and Southeast Asia where it is used in cooking and as a condiment.\r\n\r\nSoy sauce is traditionally divided into five main categories depending on differences in ingredients and method of production. The main flavor ingredients are soybean and wheat. Varieties such as "Shanghai soy sauce"(上海醬油) tend to be sweeter and less salty. "Tamari"(醬油) soy sauce is made with little or no wheat, and premium varieties of soy sauce are aged longer than regular soy sauce. The U.S. Food and Drug Administration (FDA) classifies soy sauce into two categories: "regular"(made with soy and wheat) and "tamari"(made with little or no wheat).',
