  //     title: 'Lillet',
  //     description:
  //       "Lillet (French pronunciation: ​[li'le]) is a brand of French apéritif wine. It is a blend of 85 Bordeaux region wines (Semillon for the Blanc and for the Rosé, Merlot for the Rouge) and 15 macerated liqueurs, mostly citrus liqueurs (peels of sweet oranges from Spain and Morocco and peels of bitter green oranges from Haiti). These liqueurs are made by cold macerating fruit peels in pure distilled spirits for several months.\r\n\r\nLillet is usually served well chilled as an apéritif with orange peel. It is also served on the rocks or in a variety of cocktails.\r\n\r\nThe Blanc and Rosé varieties are quinine-free. Lillet Blanc was introduced in 1887 by Raymond and Paul Lillet, fine wine merchants in Podensac, south of Bordeaux, who founded the company Lillet Frères. Lillet Rosé was introduced in 2011. Lillet Rouge, originally called Kina Lillet, is a sweet, aromatized wine.\r\n\r\nLillet is a protected trademark of the company Maison Lillet. The name Lillet is pronounced lee-lay. The company is owned and managed by the Lillet and Böhm families. Lillet is distributed in 80 countries and is produced in Podensac. The production of Lillet consists of several steps: wine is mixed with liqueurs, then stirred in oak vats until blended, then stored in oak vats for 8 to 12 months, then bottled.",
