  //     title: 'Cardamom',
  //     description:
  //       "Cardamom is a spice made from the seed pods of Globalplants in the ginger family. Cardamom pods are spindle-shaped and have a triangular cross-section. The pods contain a number of seeds, but the entire cardamom pod can be used whole or ground. The seeds are small and black, while the pods differ in color and size by species.\r\n\r\nCardamom is the world's third-most expensive spice, surpassed in price per weight only by vanilla and saffron.\r\n\r\nCardamom is used to a wide extent in savoury dishes. In Asia both types of cardamom are widely used in both sweet and savory dishes, particularly in the south. Both are frequent components in spice mixes, such as Indian and Nepali masalas and Thai curry pastes. Green cardamom is often used in traditional Indian sweets and in masala chai (spiced tea). Both are also often used as a garnish in basmati rice and other dishes. Individual seeds are sometimes chewed and used in much the same way as chewing gum.\r\n\r\nIt is used as a spice in some medicines. Cardamom has a strong, unique taste, with an intensely aromatic, resinous fragrance. Black cardamom has a distinctly more smoky, though not bitter, aroma, with a coolness some consider similar to mint.\r\n\r\nGreen cardamom is one of the most expensive spices by weight, but little is needed to impart flavor. It is best stored in the pod as exposed or ground seeds quickly lose their flavor. Grinding the pods and seeds together lowers both the quality and the price. For recipes requiring whole cardamom pods, a generally accepted equivalent is 10 pods equals 1 1/2 teaspoons of ground cardamom.",
