  //     title: 'Celery salt',
  //     description:
  //       'Celery salt is a seasoned salt used as a food seasoning, made from ground seeds, which may come from celery or its relative lovage. It is also sometimes produced using dried celery or seed oleoresin.\r\n\r\nCelery salt is an ingredient of the Bloody Mary cocktail and the Caesar cocktail. It is also commonly used to season the Chicago-style hot dog, salads, and stews. It can also be used to liven up a coleslaw. It is a primary ingredient in Old Bay brand seasoning.\r\n\r\nCelery salt is used by food producers to prevent spoilage while avoiding listing sodium nitrate on the ingredients list directly. It is sometimes used (incorrectly) as a substitute for table salt in recipes, although it contains less sodium per unit weight.',
